@mixin medium {
    @media (max-width: #{$MEDIUM_WIDTH}) {
      @content;
    }
}
@mixin medium-sm {
  @media (max-width: 1120px) {
    @content;
  }
}
@mixin tablet {
  @media (max-width: 1023px) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: 767px) {
    @content;
  }
}


